.app-wrapper {
    width: 1600px;
    margin: auto;
    display: grid;
    grid-template-areas:
            "h h"
            "n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
}

.app-wrapper-content {

    grid-area: c;
    background-color: cornflowerblue;
}